import {
    AfterContentInit,
    AfterViewInit,
    Component,
    ContentChild,
    EventEmitter,
    HostBinding,
    Input,
    ViewChild,
    Output,

    trigger,
    state,
    style,
    transition,
    animate
} from '@angular/core';
import { DialogTitleBarComponent } from './dialog-titlebar.component';

import '@telerik/kendo-theme-default/styles/packages/dialog';
/**
 * Represents the Kendo UI Dialog component for Angular.
 */
@Component({
  animations: [
    trigger('overlayAppear', [
      state('in', style({opacity: 1})),
      transition('void => *', [
        style({opacity: .1}),
        animate('.3s cubic-bezier(.2, .6, .4, 1)')
      ])
    ]),
    trigger('dialogSlideInAppear', [
      state('in', style({transform: 'translate(-50%, -50%)'})),
      transition('void => *', [
        style({transform: 'translate(-50%, -60%)'}),
        animate('.3s cubic-bezier(.2, 1, .2, 1)')
      ])
    ])
  ],
  selector: 'kendo-dialog',
  template: `
    <div class="k-overlay" @overlayAppear></div>

    <div class="k-widget k-dialog k-window k-centered k-dialog-centered" @dialogSlideInAppear>

      <kendo-dialog-titlebar *ngIf="title">{{title}}</kendo-dialog-titlebar>
      <ng-content select="kendo-dialog-titlebar" *ngIf="!title"></ng-content>

      <div class="k-window-content">
        <ng-content></ng-content>
      </div>

      <ng-content select="kendo-dialog-actions"></ng-content>

    </div>
  `
})
export class DialogComponent implements AfterContentInit, AfterViewInit {
  /**
   * Defines the title of the component.
   */
  @Input('title') public title: string;
  /**
   * Fires when the user clicks on the **Close** button of the Dialog.
   */
  @Output() public onClose: EventEmitter<any> = new EventEmitter();

  @ContentChild(DialogTitleBarComponent)
  protected titlebarContent: DialogTitleBarComponent;

  @ViewChild(DialogTitleBarComponent)
  protected titlebarView: DialogTitleBarComponent;

  public ngAfterContentInit(): void {
    this.bubbleOnClose(this.titlebarContent);
  }

  public ngAfterViewInit(): void {
    this.bubbleOnClose(this.titlebarView);
  }

  @HostBinding('class') get className(): string {
    return 'k-dialog-wrapper';
  }

  private bubbleOnClose(titlebar: DialogTitleBarComponent): void {
    if (titlebar) {
      titlebar.onClose.subscribe((e) => this.onClose.emit(e));
    }
  }
}




